import React, {Fragment, useRef, useEffect} from 'react';

import reviews from './google-reviews.json';

import s from './google-reviews.module.css';

// import photos
import photo1 from './photos/photo1.png';
import photo2 from './photos/photo2.png';

const photos = {photo1, photo2};

export default () => {
	const reviewsRef = useRef();

	/* Hide the Show More button if all the content is visible. */
	useEffect(function () {
		let children = reviewsRef.current.childNodes;
		for (let child of children) {
		    const wrapper = child.querySelector('.paragraphWrapper');
			const notOverflowing = child.clientHeight > wrapper.scrollHeight;
			if (notOverflowing) {
				const showMoreButton = child.querySelector('button');
				if (showMoreButton) {
					showMoreButton.style.display = 'none';
				}
			}
		}
	});

	function handleShowMore({paragraphWrapperIndex, showMoreButtonIndex}) {
		const wrapper = document.getElementById(paragraphWrapperIndex);
		const showMoreButton = document.getElementById(showMoreButtonIndex);
		wrapper.style.height = 'auto';
		showMoreButton.style.display = 'none';
	}

	return (
		<section className={s.googleReviewSection} ref={reviewsRef}>
			{
				reviews.map((review, rIndex) => {
					const articleIndex = `article-google-reviews-${rIndex}`;
					const paragraphWrapperIndex = `${articleIndex}-paragraph-wrapper`;
					const showMoreButtonIndex = `${articleIndex}-show-more-button`;
				    return (
						<article id={articleIndex} className={s.article} key={rIndex}>
							<div className={s.header} key={rIndex}>
								<img className={s.photo} src={photos[review.photo]}/>
								<b>{review.name}</b>
							</div>
							<div id={paragraphWrapperIndex} className={`paragraphWrapper ${s.paragraphWrapper}`}>
								<div>{review.paragraphs.map((paragraph, pIndex) => (<p key={pIndex}>{paragraph}</p>))}</div>
							</div>
							<button id={showMoreButtonIndex} onClick={() => {handleShowMore({paragraphWrapperIndex, showMoreButtonIndex});}}>Show More</button>
						</article>
					);
				})
			}
		</section>
	);
};