import React, {useRef, useEffect, Fragment, useState} from 'react';

import s from './why.module.css';
import sc from '../common.module.css';

export default function (props) {
	const {scrollIntoView} = props;
	const sectionRef = useRef();
	const sectionWrapperRef = useRef();
	const lastSection = useRef();
	const [whyWrapperHeight, setWhyWrapperHeight] = useState('20');
	const [showLoadMoreButton, setShowLoadMoreButton] = useState(true);
	useEffect(function () {
		if (scrollIntoView && sectionRef.current) {
			sectionRef.current.scrollIntoView();
		}
	});

	function handleShowMore() {
		setShowLoadMoreButton(false);
		setWhyWrapperHeight('auto');
	}

	return <Fragment>
		<div id="why" className={sc.topSectionSpacer}/>
		<section className={s.whySection} ref={sectionRef}>
			<h1 className={s.whyHeader}>Why Soulgineering?</h1>
			<div ref={sectionWrapperRef}
				className={s.whySectionWrapper}
				style={{
					height: showLoadMoreButton ? `${whyWrapperHeight}vh` : 'unset',
					overflowY: showLoadMoreButton ? 'hidden' : 'unset'
				}}>
				<p>Soulgineering is the only personal development program using a combination of Astrology, Coaching,
                    Mindfulness, and Logical Reasoning.</p>
				<p>Most people don’t understand themselves. This is because we have been far too influenced by others in
                    our
                    lives—parents, friends, the news, how we were treated as a child, and so on. Our inner voice is hard
                    to
                    hear over the voices of all those who have influenced us.
                    Not to mention the noisy voices of fear and doubt. Which of these voices speaks the loudest when
                    taking
                    a traditional personality test?
                    It’s hard to know.</p>
				<p>The problem with traditional personality assessments is that they rely solely on your feedback. But,
                    if
                    you understand yourself so well, then why would you need to take a personality assessment in the
                    first
                    place? The truth is, you are still searching for answers that no one else can give you. But, you are
                    struggling to find them for yourself.</p>
				<p>That is why we started Soulgineering. You may not realize it, but the gravitational bodies around the
                    earth have a huge impact on your body and spirit at the exact moment in space and time when you
                    entered
                    the earth. Much wisdom can be gained from knowing the positions of the planets when you were
                    born.</p>
				<p>When most people think of Astrology, they get hung up on their sun sign alone. Horoscopes seem way
                    too
                    general, because they are. They don’t look into the other planets or how strong an influence the
                    planet
                    had, and so most horoscopes are shallow and inaccurate, to say the least.</p>
				<p>That is why we coach you on the wisdom of Astrology as it relates to YOU. We have seen time and time
                    again that the astrological birth chart, or natal chart, reveals truths about the inner self that
                    most
                    people are unaware of, denying, or forgot. We use coaching and traditional assessments to test the
                    astrological data. We practice mindfulness because change starts with a mindset, and we believe
                    meditation is one of the most powerful ways to shift into a new consciousness.</p>
				<p ref={lastSection}>Don’t just survive. Thrive.<br/>
                    Be authentically and unapologetically YOU.<br/>
                    You can have the relationships, the job, the life you have always wanted.<br/>
                    The answers are inside you already.<br/>
                    Soulgineering will give you the tools you need to start digging and help you with the debugging.</p>
			</div>
			{showLoadMoreButton && <button className={s.loadMoreButton} onClick={handleShowMore}>Show More</button>}
		</section>
	</Fragment>;
};
